<template>
  <div class="m-layout">
    <div class="query">
      <el-form :model="query" class="demo-form-inline">
        <el-form-item label="">
          <el-select v-model="query.type" clearable placeholder="">
          <el-option
            v-for="item in optionsType"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="query.keyword" clearable placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <el-input  v-model="query.value" placeholder=""></el-input>
        </el-form-item>
        <div class="btn">
          <el-button type="primary" icon="el-icon-search" @click="handleQuery"></el-button>
        </div>
      </el-form>
    </div>
    <div class="operation">
      <el-button type="primary" icon="el-icon-plus" @click="handleCreate"></el-button>
      <el-button type="primary" icon="el-icon-download" @click="handleCreate">Excel</el-button>
    </div>
    <div class="list">
      <el-table
      :data="personList"
      :border="true"
      style="width: 100%">
      <el-table-column
        prop="name"
        label=""
        width="80">
      </el-table-column>
      <el-table-column
        prop="sex"
        label=""
        :formatter="formatSex"
        width="60">
      </el-table-column>
      <el-table-column
        prop="age"
        width="60"
        label="">
      </el-table-column>
      <el-table-column
        prop="card_id"
        label="">
      </el-table-column>
      <el-table-column
        prop="phone"
        label="">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <div>
            <el-button v-if="!scope.row.credit_id"
            size="mini"
            type="primary"
            @click="handleAddCredit(scope.$index, scope.row)"></el-button>
            <el-button
            size="mini"
            type="primary"
            @click="handleModify(scope.$index, scope.row)"></el-button>
            <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"></el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
    </div>
    <div class="page">
      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="query.pageIndex"
          :page-sizes="[10, 20, 50, 100]"
          :page-size="10"
          layout="total,sizes, prev, pager, next"
          :total="responseQuery.totalCount">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  computed: {
    personList: {
      get() {
        return this.$store.state.person.list || [];
      }
    }
  },
  data() {
    return {
      query: {
        pageIndex: 1,
        pageSize: 10,
        type:'',
        keyword: '',
        value: ''
      },
      responseQuery: {
        totalCount: 0,
        totalPageCount: 0
      },
      options: [{
        value: 'name',
        label: ''
      }, {
        value: 'card_id',
        label: ''
      }],
      optionsType: [{
        value: 'credit',
        label: ''
      },{
        value: 'loans',
        label: ''
      }]
    }
  },
  methods: {
    formatSex(row) {
      return ['', ''][row.sex];
    },
    handleCurrentChange(val) {
      this.query.pageIndex = val;
      this.handleQuery();
    },
    handleSizeChange(val) {
      this.query.pageSize = val;
      this.handleQuery();
    },
    handleAddCredit(index, row) {
      this.$router.push('/customer/person/addCredit/' + row.id);
    },
    handleCreate() {
      this.$router.push('/customer/person/create');
    },
    handleModify(index, row) {
      this.$router.push('/customer/person/modify/' + row.id);
    },
    handleDelete(index, row) {
      this.$store.dispatch('DelPerson', {
        params: {
          id: row.id
        }
      })
    },
    handleQuery() {
      this.$store.dispatch('GetPersonList', Object.assign({}, this.query))
        .then(this.listLoadCb);
    },
    listLoadCb(data) {
      this.responseQuery.totalCount = data.query.totalCount;
      this.responseQuery.totalPageCount = data.query.totalPageCount
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$store.dispatch('GetPersonList', Object.assign({}, vm.query))
        .then(vm.listLoadCb.bind(vm));
    });
  }
}
</script>




