<template>
  <div class="m-form-modify">
    <el-form :model="ruleForm" :inline="true" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
      <div class="title"></div>
      <el-form-item label="" prop="name">
        <el-input v-model="ruleForm.name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="age">
        <el-input v-model="ruleForm.age"></el-input>
      </el-form-item>
      <el-form-item label="" prop="sex">
        <el-radio-group v-model="ruleForm.sex">
          <el-radio :label="male"></el-radio>
          <el-radio :label="female"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="card_id">
        <el-input v-model="ruleForm.card_id"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model="ruleForm.phone"></el-input>
      </el-form-item>
      <el-form-item label="" prop="hk_address">
        <el-input v-model="ruleForm.hk_address"></el-input>
      </el-form-item>
      <el-form-item label="" prop="home_address">
        <el-input type="textarea" v-model="ruleForm.home_address"></el-input>
      </el-form-item>
      <el-form-item label="" prop="unit_address">
        <el-input type="textarea" v-model="ruleForm.unit_address"></el-input>
      </el-form-item>
      <el-form-item label="" prop="income">
        <el-input v-model="ruleForm.income"></el-input>
      </el-form-item>
       <el-form-item label="" prop="spouse_id">
        <el-input v-model="ruleForm.spouse_id"></el-input>
      </el-form-item>
      <div v-if="ruleForm.credit_id">
        <div class="title"></div>
        <el-form-item label="" prop="credit_id">
          <el-input v-model="ruleForm.credit_id" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="credit_starttime">
          <el-date-picker
            v-model="ruleForm.credit_starttime"
            type="date"
            value-format="timestamp"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="credit_expiretime">
          <el-date-picker
            v-model="ruleForm.credit_expiretime"
            type="date"
            value-format="timestamp"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="credit_adjuster">
          <el-input v-model="ruleForm.credit_adjuster"></el-input>
        </el-form-item>
        <el-form-item label="" prop="credit_level">
          <el-input v-model="ruleForm.credit_level"></el-input>
        </el-form-item>
      </div>
      <div class="title"></div>
      <el-form-item label="" prop="loans_count">
        <el-input v-model="ruleForm.loans_count"></el-input>
      </el-form-item>
      <el-form-item label="" prop="loans_deadline">
        <el-input v-model="ruleForm.loans_deadline"></el-input>
      </el-form-item>
      <el-form-item label="" prop="loans_rate">
        <el-input v-model="ruleForm.loans_rate"></el-input>
      </el-form-item>
      <el-form-item label="" prop="lending_date">
        <el-date-picker
          v-model="ruleForm.lending_date"
          type="date"
          value-format="timestamp"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="" prop="loans_balance">
        <el-input v-model="ruleForm.loans_balance"></el-input>
      </el-form-item>
      <el-form-item label="" prop="loans_subject">
        <el-input v-model="ruleForm.loans_subject"></el-input>
      </el-form-item>
      <el-form-item label="" prop="repayment_method">
        <el-input v-model="ruleForm.repayment_method"></el-input>
      </el-form-item>
      <el-form-item label="" prop="loans_bondsman_ids">
        <el-input v-model="ruleForm.loans_bondsman_ids"></el-input>
      </el-form-item>
      <div class="form-button">
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </div>
    </el-form>
  </div>
</template>
<script>
import _ from 'lodash';
export default {
  data() {
    return {
      male: 0,
      female: 1,
      id: '',
      ruleForm: {
        name: '',
        age: '',
        sex: 0,
        card_id: '',
        phone: '',
        hk_address: '',
        home_address: '',
        unit_address: '',
        income: '',
        spouse_id: '',
        credit_id: '',
        credit_starttime: '',
        credit_expiretime: '',
        credit_adjuster: '',
        credit_level: '',
        loans_count: '',
        loans_deadline: '',
        loans_rate: '',
        loans_bondsman_ids: '',
        lending_date: '',
        loans_balance: '',
        loans_subject: '',
        repayment_method: ''
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 8, message: ' 2  8 ', trigger: 'blur' }
        ],
        sex: [
          { required: true, message: '', trigger: 'change' }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      const data = this.ruleForm;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$store.dispatch('ModifyPerson', { data, params: { id: this.id } }).then((result) => {
            this.$router.push('/customer/person/list');
          }).catch(function () {
            return new Error('reject again in nested Promise');
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.id = to.params.id;
      vm.$store.dispatch('GetPersonItem', {
        params: {
          id: to.params.id
        }
      }).then(data => {
        vm.ruleForm = _.merge({}, data);
      });
    });
  }
}
</script>
<style lang="scss" scoped>
.el-form {
  width: 880px;
  .title {
    padding-left: 10px;
    margin-bottom: 20px;
    font-size: 14px;
    font-weight: bold;
    color: #555;
    border-left: 3px solid #efefef;
    line-height: 20px;
  }
  .form-button {
    padding-top: 20px;
    text-align: center;
  }
}
</style>
<style lang="scss">
.m-form-modify {
  .el-form-item__content {
    width: 300px;
  }
  .el-form-item__label {
    font-weight: normal;
  }
}
</style>





